import '../css/bootstrap.min.css'
import '../css/w3.css'
import './Navbar.css'

const Nav = (props) => {
    const sideBar = () => {
        var x = document.getElementById("sideBar");
        console.log("clicked")
        if (x.className.indexOf("w3-show") === -1) {
            x.className += " w3-show";
        } else { 
            x.className = x.className.replace(" w3-show", "");
        }
    }
    return(
        <nav className="navbar navbar-expand-lg navbar-dark">
            <div className="container-xxl">
                <div id="sideBar" className="w3-bar-block w3-white w3-hide w3-hide-large w3-large container-xxl">
                    <div className="w3-sidebar w3-bar-block w3-card w3-animate-left w3-center">
                        <a className="w3-bar-item w3-button w3-padding-large font-patua-one" href="#none" onClick={sideBar.bind(this)}>Close<i className="material-icons align-middle">close</i></a>
                        {
                            props.menu.map((data) => {
                                return(
                                    <a href={data.href} className="w3-bar-item w3-button w3-padding-large font-patua-one" key={data.key}>{data.item}</a>
                                )
                            })
                        }
                    </div>
                </div>
                
                <a className="navbar-brand" href="#top">
                    <div className="d-flex align-items-center">
                        <img src="logo192.png" alt="QMG Solutions" className="d-inline-block align-text-top"/>
                        <div className="logo-name ms-2">
                            <div className="title font-asap-bold">{props.title}</div>
                            <div className="subtitle oswald">{props.subtitle}</div>
                        </div>
                    </div>
                </a>
                <button className="navbar-toggler" type="button" onClick={sideBar.bind(this)} ><span className="material-icons">menu</span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ms-auto">
                        {
                            props.menu.map((data) => {
                                return(
                                    <li className="nav-item" key={`nav-${data.key}`}>
                                        <a className="nav-link font-raleway-medium" 
                                            href={data.href} 
                                            aria-current="page">{data.item}</a>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        </nav>
    );
}
export default Nav;