import React from 'react';
import parser from 'html-react-parser';
import './Carousel.css'
class Carousel extends React.Component {
    
    componentDidMount(){
        var c = document.getElementsByClassName("carousel-item")[0]
        c.className += " active"
        this.showSlides(this.slideIndex = 0)
    }
    
    plusSlides(n){
        if(n.target.className==="next")
            this.showSlides(this.slideIndex += 1);
        else
            this.showSlides(this.slideIndex -= 1);
    }
    currentSlide(n){
        this.showSlides(this.slideIndex = parseInt(n.target.id));
    }
    showSlides(n){
        var i;
        var slides = document.getElementsByClassName("carousel-item");
        var dots = document.getElementsByClassName("dot");

        if (n > slides.length) {
            this.slideIndex = 1
        }
        if (n < 1) {
            this.slideIndex = slides.length
        }
        for (i = 0; i < slides.length; i++) {
            slides[i].style.display = "none";
        }
            
        for (i = 0; i < dots.length; i++) {
            dots[i].className = dots[i].className.replace(" active", "");
        }
        
        try {
            if(slides !== undefined)
                slides[this.slideIndex-1].style.display = "block";
            if(dots !== undefined)
                dots[this.slideIndex-1].className += " active";    
        } catch (error) {
            console.log(error.message)
        }
        
    }
    render(){
        return(
            <section className="About-Us">
                {/* ⚠️ Attention To container size */}
                <div className="container-xxl text-white bg-dark">
                    <div id="carouselSlides" className="carousel slide" data-bs-ride="carousel">
                        <div className="carousel-inner">
                            {
                                this.props.slides.map((data)=>{
                                    return(
                                        <div className="carousel-item" key={data.key}>
                                            <div className="info">
                                                <h3 className="title text-center font-asap-bold uppercase">{parser(data.title)}</h3>
                                                <div className="content font-patua-one">{parser(data.description)}</div>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                        <a className="prev" href="#null " onClick={this.plusSlides.bind(this)}>&#10094;</a>
                        <a className="next" href="#null" onClick={this.plusSlides.bind(this)}>&#10095;</a>
                            
                    </div>
                </div>
            </section>
        )
    }
};
export default Carousel;