import './Breadcrumb.css'
const Breadcrumb = (props) => {
    return(
        <ul id="filters" className="breadcrumb align-items-center justify-content-center">
        {
            props.list.map((data)=>{
                return(
                    <li key={data.key}><a href="#filters" className={props.linkFont}>{data.type}</a></li>
                );
            })
        }
        </ul>
    )
}
export default Breadcrumb