const FormGroup = (props) => {
    return(
        <div className="mb-3">
            <label htmlFor={props.id} className={`form-label ${props.labelFont}`}>{props.label}</label> 
            {
                props.type === "textarea" ? 
                    <textarea id={props.id} name={props.id} rows={props.rows} className={`form-control ${props.inputFont}`}></textarea> 
                    : <input id={props.id} name={props.id} placeholder={props.label} type={props.type ? props.type : "text"} className={`form-control ${props.inputFont}`} /> 
            }    
        </div>
    );
}
export default FormGroup;